        <h1 class="display-3">Conclusions</h1>
  
        <h5>We saw the largest increase in number of WiFi users when there were community driven events. We believe we can empower the businesses to attract community to engage with them to help promote other activities, for the benefit of both the business and community at large. Community events, such as Fridays After Five, consistently draw many people to the Downtown Mall. Events such as these can be expanded to bring the community and businesses together. Larger festivals such as the Tom Tom Festival even draw people from outside of Charlottesville.</h5> 
        <br>

        <img width="180" height="180" src="static/static_web_images/business_chain_people_logo.png" class="img-fluid"> <br>

        <b-card header="<h5><b>Five key insights that were revealed from the data:</b></h5>" class="border-secondary mt-2 img-fluid">
          <!-- <h5><u>Five key insights that were revealed from the data:</u></h5><br><br> -->

          1. <b>Know your visitors:</b> They tend to use Apple devices and businesses on the Downtown mall could leverage this information by creating more Apple-targeted products or offering related services.<br>
          2. <b>Engage with your visitors</b> on platforms they use. One way is to have business incentivize users to engage over social media (Facebook or Instagram for example).<br>
          3. <b>Impact of Weather:</b> Provide rainy day discounts because the data has shown that visitors are less likely to come out (post it on your Facebook, because they use Facebook).<br>
          4. <b>Day of the Week:</b> Knowing Sundays are a low traffic day, perhaps encourage restaurants to offer a Brunch on Sundays.<br>
          5. <b>Type of Event:</b> Continue to draw in visitors and wifi users through annual events. Music and technology related events tend to draw larger crowds.<br><br>

          When you better understand your users you are better able to cater to their needs. It's clear from the data that community events are what drives the highest usage and <b>connectivity</b> between businesses and WiFi users who have the potential to shop there.<br><br> 
