<script>
    import LineExample from './charts/LineExample'
    import PieExample from './charts/PieExample'
    import BarExample from './charts/BarExample'
    import { Carousel, Slide } from 'vue-carousel'

    export default {
      name: 'story',
      data () {
        return {
          labels_app: ['label'],
          data_app: [12],
          // Title: "Social Media App Usage"
          labels_app_social: ['Facebook', 'Tumblr', 'Instagram', 'Snapchat'],
          data_app_social: [42.8, 5.6, 30.2, 21.4],
          pie_colors_app_social: ['#3b5998', '#35465c', '#c13584', '#fffc00'],
          // Title: "Music App Usage"
          labels_app_music: ['iTunes', 'Spotify', 'Misc. audio', 'Pandora'],
          data_app_music: [64.3, 20.1, 8.4, 7.2],
          pie_colors_app_music: ['#6762a6', '#1db954', '#28353d', '#005483'], // '#C36891',
          // Title: Video App Usage
          // TODO: social media icons
          labels_app_video: ['YouTube', 'Misc. video', 'Netflix'],
          data_app_video: [68.6, 13.5, 17.8],
          pie_colors_app_video: ['#ff0000', '#564d4d', '#831010'], // '#db0000'
          // Top Device Categories
          labels_mobile: ['Apple', 'Samsung', 'Murata Mfg', 'Intel', 'Kyocera', 'Motorola', 'HTC', 'Huawei Tech'],
          data_mobile: [6544, 742, 420, 531, 140, 153, 360, 119],
          pie_colors_mobile: ['#5270ff', '#E46651', '#00D8FF', '#41B883', '#00BCD4', '#03A9F4', '#009688', '#673AB7', '#3F51B5', '#2196F3', '#F44336', '#E91E63', '#9C27B0', '#4CAF50', '#FFEB3B', '#CDDC39', '#FFC107', '#FF9800', '#FF5722', '#795548', '#607D8B', '#9E9E9E'],
          // Top Events
          labels_events: ['Festivals', 'Fridays After Five', 'General', 'Holiday', 'Kids Event', 'Movie', 'Speaker/Performance', 'Theme Week'],
          data_events: [171, 212, 111, 102, 90, 101, 136, 95],
          green_colors_events: ['#C2E79A', '#92C060', '#699A33', '#457313', '#284D00', '#C2E79A', '#92C060', '#699A33'],
          // Clients by Days of the Week
          labels_dates: ['Mon', 'Tues', 'Wed', 'Thurs', 'Fri', 'Sat', 'Sun'],
          data_dates: [96, 112, 121, 119, 154, 134, 94],
          default_pie_colors: ['#41B883', '#E46651', '#00D8FF', '#DD1B16', '#00BCD4', '#03A9F4', '#009688', '#673AB7', '#3F51B5', '#2196F3', '#F44336', '#E91E63', '#9C27B0', '#4CAF50', '#FFEB3B', '#CDDC39', '#FFC107', '#FF9800', '#FF5722', '#795548', '#607D8B', '#9E9E9E']
        }
      },
      components: {
        LineExample,
        PieExample,
        BarExample,
        Carousel,
        Slide
      }
}
</script>


