<script>
import { Pie } from 'vue-chartjs'

export default {
  extends: Pie,
  props: ['labels', 'dataPoints', 'pieColors'],
  mounted () {
    this.renderChart({
      labels: this.labels,
      datasets: [
        {
          backgroundColor: this.pieColors,
          data: this.dataPoints
        }
      ]
    }, {responsive: true, maintainAspectRatio: false})
  }
}
</script>



