import Vue from 'vue'
import Router from 'vue-router'

// Containers
import Full from '@/containers/Full'

// Views
import Story from '@/views/Story'
import ModelPage from '@/views/Model'
import Home from '@/views/Home'
import Data from '@/views/Data'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/',
      redirect: '/story',
      name: 'Home',
      component: Full,
      children: [
        {
          path: 'story',
          name: 'Story',
          component: Story
        },
        {
          path: 'model',
          name: 'Model',
          component: ModelPage
        },
        {
          path: 'data',
          name: 'Data',
          component: Data
        }
      ]
    }
  ]
})



